.nav {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: $height_navbar;
  background-image: linear-gradient(to right, $gradient-color_background);
  color: $color_white;
  text-transform: uppercase;
  font-weight: 500;

  @extend %flex-row-center;

  &+main {
    padding-top: $height_navbar;
  }

  &__container {
    @extend %flex-row-between;
    @extend %layout_container;
  }

  &__link {
    display: flex;

    a {
      margin-left: $row-size_col-margin;
      color: $color_white;
    }
  }
}
