.section {
  position: relative;
  padding-bottom: 40px;

  &__container {
    @extend %layout_container;

    &--row {
      overflow: hidden;

      @extend %flex-wrap;

      >section {
        flex: 1;
        margin: $row-size_col-margin $row-size_col-margin*2;

        @for $i from 1 through 6 {
          &.flex-#{$i} {
            flex: #{$i};
          }
        }
      }
    }

    &--full {
      max-width: none;
      width: 100%;

      img {
        width: 100%;
      }
    }
  }

  &__title {
    display: block;
    padding: $layout-size_small 0;
    border-bottom: $color_white solid 1px;
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 1.25rem;

    &--center {
      text-align: center;
    }

    &--white {
      color: $color_white;
    }
  }
}
